    static createShader(gl: WebGLRenderingContext, type: WebGLShader, shaderString: string) {
        const shader = gl.createShader(type as unknown as number);
        if (shader === null) { return null; }
        gl.shaderSource(shader, shaderString);
        gl.compileShader(shader);
        const suc = gl.getShaderParameter(shader, gl.COMPILE_STATUS);
        if (suc) { return shader; }
        console.log(gl.getShaderInfoLog(shader));
        gl.deleteShader(shader);
        return null;
    }
    static createShaderFromPath(gl: WebGLRenderingContext, type: WebGLShader, url: string) {
        const shader = gl.createShader(type as unknown as number);
        if (shader === null) { return null; }
        gl.shaderSource(shader, GLShaderCommon.getSourceSynch(url) as string);
        gl.compileShader(shader);
        const suc = gl.getShaderParameter(shader, gl.COMPILE_STATUS);
        if (suc) { return shader; }
        console.log(gl.getShaderInfoLog(shader));
        gl.deleteShader(shader);
        return null;
    }

    static getSourceSynch(url: string) {
        const req = new XMLHttpRequest();
        req.open('GET', url, false);
        req.send(null);
        return (req.status === 200) ? req.responseText : null;
    }

