import { WebGL1 } from '..';

export class CoreWebgl {
    app: WebGL1;

    gl: WebGLRenderingContext;
    canvas: HTMLCanvasElement;

    constructor(app: WebGL1) {
        this.app = app;

        this.canvas = document.createElement('canvas');
        if (this.app.divHost) {
            this.app.divHost.appendChild(this.canvas);
            this.canvas.width = this.app.divHost.clientWidth;
            this.canvas.height = this.app.divHost.clientHeight;
        }
        const gl = this.canvas.getContext('webgl');
        if (gl) { this.gl = gl; }

        this.loop(this.gl);
    }
    loop(gl: WebGLRenderingContext) {
        this.clear(gl);
        // requestAnimationFrame(() => { this.loop(gl); });
        // this.ctx.clearRect(0, 0, this.ctx.canvas.width, this.ctx.canvas.height);
        // this.app.Loop(this.ctx);
        // console.log('working');
    }
    clear(gl: WebGLRenderingContext) {
        gl.clearColor(0.5, 1.0, 1.0, 1.0);
        gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
    }
}
